.seo{
  margin-bottom: 57px;
  @media all and (max-width: 1920px){
    margin-bottom: px(57);
  }
  @media all and (max-width: 768px){
    margin-bottom: px(50);
  }
  @media all and (max-width: 560px){
    margin-bottom: px(40);
  }
  b, strong{
    font-weight: bolder;
  }
  h1{
    font-size: 48px;
    color: #181717;
    margin-bottom: 35px;
    font-weight: 700;
    line-height: normal;
    text-align: left;
    @media all and (max-width: 1920px){
      font-size: px(48);
      margin-bottom: px(35);
    }
    @media all and (max-width: 1600px){
      font-size: px(42);
      margin-bottom: px(35);
    }
    @media all and (max-width: 1440px){
      font-size: px(38);
      margin-bottom: px(32);
    }
    @media all and (max-width: 1200px){
      font-size: px(34);
      margin-bottom: px(29);
    }
    @media all and (max-width: 1024px){
      font-size: px(30);
      margin-bottom: px(26);
    }
    @media all and (max-width: 768px){
      font-size: px(26);
      margin-bottom: px(23);
    }
    @media all and (max-width: 500px){
      font-size: px(22);
      margin-bottom: px(10);
    }
    strong{
      color: #C4B39B;
    }
  }
  &__container{
    @media all and (max-width: 560px){
      font-size: px(16);
    }
    @media all and (max-width: 480px){
      font-size: px(14);
    }
    strong{
      font-weight: 600;
    }
    a{
      color: rgba(210, 145, 222, 1);
    }
    p{
      &:last-child{
        margin-bottom: 0;
      }
      font-weight: 400;
      font-size: 18px;
      line-height: 141.9%;
      color: #000000;
      margin-bottom: 1em;
      @media all and (max-width: 1920px){
        font-size: px(18);
      }
      @media all and (max-width: 1600px){
        font-size: px(17);
      }
      @media all and (max-width: 1400px){
        font-size: px(16);
      }
      @media all and (max-width: 1204px){

      }
      @media all and (max-width: 480px){
        font-size: px(14);
      }
    }
    strong{
      font-family: "AvenirNext-Bold", sans-serif;
    }
    ul, ol{
      list-style: disc;
      padding-left: 20px;
      margin-bottom: 1em;
      @media all and (max-width: 1920px){
        padding-left: px(20);
      }
      li{
        font-weight: 400;
        font-size: 18px;
        line-height: 141.9%;
        color: #000000;
        margin-bottom: 1em;
        @media all and (max-width: 1920px){
          font-size: px(18);
        }
        @media all and (max-width: 1600px){
          font-size: px(17);
        }
        @media all and (max-width: 1400px){
          font-size: px(16);
        }
        @media all and (max-width: 1204px){

        }
        @media all and (max-width: 500px){

        }
      }
    }
  }
  .section-subtitle{
    margin-bottom: 0;
  }
}