table {
  width: 100%;
  border-collapse: separate;
  margin-bottom: 3rem;
}
table th {
  font-weight: 400;
  padding: 19px 26px;
}
table td {
  font-weight: 400;
}
table th, table td {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 125%;
  color: #616BA0;
  padding: 1.2rem 2rem;
  text-align: left;
}
@media all and (max-width: 768px) {
  table th, table td {
    padding: 12px 28px;
  }
}
table thead th {
  padding: 4px 26px;
}
table tbody tr {
  border-radius: 10px;
  transition: 0.2s;
}
table tbody tr:hover {
  background: rgba(0, 48, 87, 0.03);
}
table tbody tr td {
  border-bottom: 1px dashed #C2D6E5;
}
table tbody tr td:first-child {
  border-radius: 10px 0 0 10px;
}
table tbody tr td:last-child {
  border-radius: 0 10px 10px 0;
}
table tbody tr td img {
  margin-right: 14px;
}
table tbody tr th {
  background: rgba(194, 214, 229, 0.31);
}
table tbody tr th:first-child {
  border-radius: 5px 0 0 5px;
}
table tbody tr th:last-child {
  border-radius: 0 5px 5px 0;
}

p {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 141.9%;
  color: #000000;
  margin-bottom: 1em;
}
