  }
}
strong {
  font-weight: 600;
}

.text {
  margin-bottom: 70px;
}
.text strong {
  font-weight: 600;
}
.text ul {
  list-style: none;
}
.text ol {
  list-style: decimal;
}
.text ul {
  list-style: square;
  padding-left: 20px;
  margin-bottom: 2.2rem;
}
.text ul li {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 140%;
  margin-bottom: 1.1rem;
}
.text ul li::marker {
  color: #ca85d5;
}
.text ul li ul, .text ul li ol {
  margin-top: 1rem;
}
.text a {
  text-decoration-line: underline;
  color: #ca85d5;
}

