  --article-card-title: #2f2f2f;
}

[data-theme=dark] {
  --main-bg: #2d2b2b;
  --main-color: #fff;
  --color-muted: #fff;
  --color-dark: #fff;
  --header-bg: #2d2b2b;
  --sidebar-bg: #000;
  --footer-bg: #212121;
  --card-bg: #434343;
  --form-control-bg: #212121;
  --article-card-title: #fff;
}

